$background: rgb(211, 211, 213);;

.Portfolio {
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    background: $background;
    font-size: 11px;
    .container {
        position: absolute;
        left: 0px;
        right: 0px;
        top: 0px;
        bottom: 0px;
        border: 3px solid rgb(255, 255, 255);
        text-align: center;
        margin: 50px;
    }
    h1 {
        font-size: 35px;
        margin-top: 0px;
        margin-bottom: 15px;
    }
    h2 {
        font-size: 20px;
        margin: 0;
    }
    h3 {
        font-size: 1em;
        -webkit-transform: scale(0.75);
        font-weight: 400;
        margin-top: -8px;
        margin-bottom: 0px;
    }
    h4 {
        font-size: 20px;
        margin-top: 15px;
        margin-bottom: 0px;
    }
    p {
        font-size: 1em;
        display: inline-block;
        width: 70%;
        margin-top: -10px;
        margin-bottom: 0px;
        span {
            font-size: 1em;
            text-align: justify;
            text-align-last: center;
            display: inline-block;
            -webkit-transform: scale(0.75);
        }
    }
    .iconContainer {
        margin-top: 70px; // margin-bottom: 50px;
    }
}

.IconWTitle {
    display: inline-block;
    width: 100px;
    margin: 0 60px;
    padding: 5px;
    border-radius: 4px;
    // transition-delay: 0.5s;
    // border: 3px solid rgb(255, 255, 255);
    background: $background;
    img {
        width: 50px;
        height: 50px;
    }
    div {
        font-size: 15px;
        font-weight: 700;
    }
}
.IconWTitle:hover {
    transition-duration:0.5s;
    box-shadow: 5px 5px 10px 5px #888888;
}
.IconWTitle:active {
    transition-duration:0.05s;
    box-shadow: 0 0;
    background: darken($background, 10%) 
}

.pan {
    width: 200px;
    height: 200px;
    position: absolute;
    bottom: 23px;
    left: 50%;
    transform: translateX(-50%);
}

@mixin img-rotate($ang) {
    -webkit-transform: rotate($ang);
    -moz-transform: rotate($ang);
    -o-transform: rotate($ang);
    -ms-transform: rotate($ang);
    transform: rotate($ang);
}

.img-rotate-90deg {
    @include img-rotate(90deg);
}

.img-rotate-180deg {
    @include img-rotate(180deg);
}

.img-rotate-270deg {
    @include img-rotate(270deg);
}