$background: rgb(202, 190, 190);

.Timeline {
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    background: $background;
    font-size: 11px;
    .container {
        position: absolute;
        left: 0px;
        right: 0px;
        top: 0px;
        bottom: 0px;
        border: 3px solid rgb(255, 255, 255);
        text-align: center;
        margin: 50px;
    }
    img {
        max-width: 100%;
        max-height: 100%;
    }
}